export function getApiUrl(path:string) {
    const public_url = document.getElementsByName("public_url")[0] as HTMLScriptElement;
    console.log({'public_url': public_url.nodeValue})
    if (public_url && public_url.nodeValue) {
        return `${public_url.nodeValue}/${path}`
    }
    else {
        // Local Development
        return `http://localhost:3825/${path}`
    }
}